.pattern {
  height: 0;
  overflow: hidden;
  position: relative;
  width: 100%; }

.pattern__wrapper {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%; }
  .pattern--trueTransition .pattern__wrapper {
    animation-direction: alternate;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-name: zoom; }

.pattern__row:nth-of-type(odd) .pattern__segment:nth-child(odd) .pattern__flip {
  transform: rotateX(180deg); }

.pattern__row:nth-of-type(odd) .pattern__segment:nth-child(even) .pattern__flip {
  transform: rotateX(180deg) rotateY(180deg); }

.pattern__row:nth-of-type(even) .pattern__segment:nth-child(even) .pattern__flip {
  transform: rotateY(180deg); }

.pattern__segment {
  float: left;
  height: 0;
  position: relative;
  transform-origin: left top; }
  .pattern--yAxis2.pattern--trueTransition .pattern__segment,
  .pattern--yAxis2.pattern--trueTransition .pattern__segment {
