.pattern__flip {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%; }

.pattern__rotate {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%; }
  .pattern__rotate--0 {
    transform: rotate(0deg); }
  .pattern__rotate--90 {
    transform: rotate(90deg); }
  .pattern__rotate--180 {
    transform: rotate(180deg); }
  .pattern__rotate--270 {
    transform: rotate(270deg); }

.pattern__svg {
  height: auto;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%; }
  .pattern__svg:nth-of-type(2) {
    transform: rotate(90deg) rotateX(180deg); }
  .pattern__svg:nth-of-type(1n+3) {
    opacity: 0.5; }
  .pattern__svg:nth-of-type(3) {
    transform: rotateX(180deg) rotateY(180deg); }
  .pattern__svg:nth-of-type(4) {
    transform: rotate(-90deg) rotateX(180deg); }

